.accordion {
  margin: 2rem 0;
  &.none-padding{
    .accordion__card{
      box-shadow: none;
      padding: 0;
      &__header {
        padding-bottom: 1rem!important;
      }
      &__body {
        margin-top: 0;
      }
    }

  }
  &__card{
    box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.08);
    border-radius: 3px;
    padding: 2rem;
    margin: 1rem 0;
    &.bg-stripes{
      border-radius: 20px;
      box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.15);
      margin-bottom: 30px;
    }
    &__header{
      display: flex;
      align-items: center;
      font-weight: bold;
      font-size: $txt_18px;
      height: 100%;
      width: 100%;
      cursor: pointer;
      @media(max-width:991px) {
        display: block;
        img {
          margin-bottom: 1rem;
        }
      }
      .icon{
        margin-right: 1rem;
        width: 6rem;
        img {
          object-fit: contain;
          width: 100%;
          max-height: 6rem;
          max-width: 5rem;
        }
      }
    }
    &__body {
      margin-top: 2rem;
    }
  }
}



